/* 1. Use a more-intuitive box-sizing model */
*, *::before, *::after {
    box-sizing: border-box;
  }
  /* 2. Remove default margin */
  * {
    margin: 0;
  }
  body {
    /* 3. Add accessible line-height */
    line-height: 1.5;
    /* 4. Improve text rendering */
    -webkit-font-smoothing: antialiased;
  }
  /* 5. Improve media defaults */
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%
  }
  /* 6. Inherit fonts for form controls */
  input, button, textarea, select {
    font: inherit;
  }
  /* 7. Avoid text overflows */
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }


/* App */
body{
    background-color: #07000d;
    background-image: url("https://www.transparenttextures.com/");
    color: white;
    font-family: 'Rubik', sans-serif;
}
/* container */
.container{
    max-width: 1200px;
    margin:0 auto;

}

/* todo list */
.title{
    text-align: center;
    font-size: 3rem;
    padding: 3rem 0;
}
.todos{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

}

.todo{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 1rem;
    background-color: rgba(0,0,0,0.1);
    width: 50%;
    font-size: 1.5rem;
}

.mutations{
  display: flex;
  gap:0.5rem;
}


.todo__status,
.todo__delete {

  border: none;
  padding: 0;
  line-height: 1;
  cursor: pointer;
  transition: all 0.2s;
}

.todo__status:hover {
  transform: scale(1.15);
}


/* form */

.form{
  display: flex;
  gap: 0.5rem;
  width:50%;
  margin: 1rem auto;
}

.form__input{
  flex:1;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  border: none;
  outline: none;
  
  
  
 

  
}
.form__button{
  border: none;
  background-color: rgba(113,0,212,0.7);
  cursor: pointer;
  color: white;
  border-radius: 0;
  transition: all 0.2s;

}

@media(max-width:420px){
  .title{
    font-size: 2.5 rem;

  }
  .form{
    width: 95%;
  }
  .todo{
    width: 95%;
    font-size: 1.2rem;
  }

}